.football {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.football__title {
  color: #000;
  margin-bottom: 36px;
  font-size: 16px;
  font-weight: 400;
}

.football__field {
  width: 720px;
  height: 220px;
  background-color: #5abb58;
  border: 2px solid #000;
  border-radius: 37px;
  position: relative;
  overflow: hidden;
}

.football__img {
  width: 50px;
  height: 50px;
  transition: transform .5s;
  position: absolute;
}

